import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import Slide from '@material-ui/core/Slide';

import { toast } from 'react-toastify';
import './styles.css';
import agent from '../../../agent';
import {

} from '../../../constants/actionTypes';
import ProductDetails from './ProductDetails';

class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      render: false,
      loading: false,
      user: {},
      product: { },
      quantity: 1,
      images: [],
    };
  }

  componentWillMount() {
    if (this.props.data) {
      this.setState({ product: this.props.data });
    }
  }

  componentWillReceiveProps(np) {
    if (np.data && np.data !== this.state.product) {
      this.setState({ product: np.data });
    }
  }

  modifyQuantity(change) {
      if(this.state.quantity +change >0 )
        this.setState({ quantity: this.state.quantity + change });
  }

  render() {
    const {
      name, desc, disc_price, price, stock, estimated_delievry, images,
    } = this.state.product;
    const { quantity } = this.state;
    return (
      <div className="public-product relative">
        {/* <ProductDetails data={this.state.product} /> */}
        <div className="product-header-img">
          {
                images.length > 0
                  ? <img src={images[0]} alt="" /> : <img src="" alt="" />
            }

        </div>

        <div className="product-pricing">
          <h3 className="ma0">
            {name}
          </h3>
          {
                disc_price === price
                  ? (
                    <b>
                        Rs.
                      {price}
                    </b>
                  )

                  : (
                    <div>
                        <div className='home-page-product-sale' >{Math.floor(((price-disc_price)/price)*100)} %off </div>
                      <b>
                        Rs.
                        {disc_price}
                      </b>
                      <strike>{` Rs. ${price}`}</strike>

                    </div>
                  )

            }
          <div className="product-bottom-div">
            <div className="flex">
              <button type="button" className='w-20' onClick={() => this.modifyQuantity(-1)}> - </button>
              <input type="number" value={quantity} className='w-60' disabled />
              <button type="button" className='w-20' onClick={() => this.modifyQuantity(1)}>  + </button>
            </div>
            <button className="product-add-to-cart"> Add to cart </button>
          </div>
        </div>


      </div>

    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,

});

const mapDispatchToProps = (dispatch) => ({

});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
